.card {
    width: 17vw;
    height: 43vh;
    border-radius: 1em;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: #080808;
    box-shadow: 0 0 2rem rgba(0, 0, 0, 0.8);
    cursor: pointer;
    transition: 0.2s ease-in-out;
    padding: 5px;
    color: #fff;
  }

  .popup{
    display: flex;
    position: absolute;
    top: 30px;
    right: 10px;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .card:hover {
    rotate: 1deg;
    scale: 1.05;
  }
  
  .card, .imgBox, .caller {
    display: flex;
    align-items: center;
  }
  
  .imgBox {
    width: 6em;
    height: 6em;
    box-shadow: 0px 0.25em 1rem rgba(255, 255, 255, 0.5);
    border-radius: 50%;
    justify-content: center;
  }
  
  .imgBox svg {
    width: 100%;
    height: 100%;
  }
  
  .name {
    width: 100%;
    text-align: center;
    font-weight: 900;
    transition: 1s ease-in-out;
  }
  
  .name .p1 {
    font-size: 1.2em;
  }
  
  .name .p2 {
    font-size: 0.8em;
    color: rgb(50, 146, 255);
  }
  
  .caller {
    width: 100%;
    flex-direction: row;
    justify-content: space-evenly;
  }
  
  .callerBtn {
    width: 2em;
    height: 2em;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 1.5rem;
    cursor: pointer;
    transition: 0.1s ease-in-out;
    animation: animate linear infinite 2s;
  }
  
  .callerBtn:hover {
    scale: 1.1;
  }
  
  #pick {
    background-color: #28ff28;
    box-shadow: 0px 3px 10px #28ff28;
  }
  
  #end {
    background-color: #ff2828;
    box-shadow: 0px 3px 10px #ff2828;
  }
  
  #end svg {
    rotate: 135deg;
  }
  
  @keyframes animate {
    0% {
      transform: translateY(0px);
    }
  
    50% {
      transform: translateY(3px) rotate(-10deg);
    }
  
    100% {
      transform: translateY(0px);
    }
  }